#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skillDescrption {
    font-weight: 200;
    font-size: 1.05rem;
    max-width: 51rem;
    padding: 0 2rem;
}

.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillBar {
    display: flex;
    margin:  1rem;
    padding:1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);
}

.skillBarImg {
    object-fit: cover;
    height: 8rem;
    width: 8rem;
    margin-right: 2rem;
}
 
.skillBarText>p{
    margin-top: 1rem;
    font-size: 0.9rem;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .skillBarText>P{
        font-size: 2vw;
    }
    
}

@media screen and (max-width:480px) {
    .skillBarText>P{
        top: 1rem;
        font-size: 3vw;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;

    }
    .skillBarText>h2{
        font-size: 5vw;
        
    }
    
}