#clients {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.clientDec {
    font-weight: 300;
    font-size: 1rem;
    padding: auto;
}
.clientImgs {
    width: 28rem;
    background: #303030;
    display: flex;
    flex-direction: column; 
    margin: 3rem;
    padding: 3rem auto;
    border-radius: 1rem;
    flex-wrap: wrap;
    text-align: center;
    align-items: center; 
}
.cilentImg {
    object-fit: cover;
    margin: 0;
    padding: 1.25rem;
    height: 25rem;
    width: 25rem;
}
.clientImgs h2 {
    margin-bottom: 1rem;
    font-size: xx-large;
}
#contact {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.contactDesc {
    font-weight: 300;
    font-size: 1rem;
    padding: 1rem;
}

.contactForm {
    margin: 1rem auto; /* Adjusted margin for centering */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 40rem;
}

.name, .email, .Msg, .phone{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;    
    color: silver;
    border: none;
    border-radius: 0.5rem;
    background-color: #404040;
}
.sumbitBtn{
    width: 15vw;
    background: silver;
    border: none;
    border-radius: 0.5rem;
    margin: 1.5rem;
    padding: 0.75rem;
    
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}



