#intro {
    height: calc(100vh - 4rem);
    width: 100%;
    max-width: 75rem;
    max-height:55rem ;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    top: 1rem;
}
.bg{
    position: absolute;
    top: 1rem;
    right: 0;
    z-index: -1;
    object-fit: contain;
    height: 100%;
    max-width: 100%;
}
.introContent{
    height:100vh ;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.hello{
    font: 1.75rem;
    font-weight: 100;
    
}

.introName{
    color:rgb(255,215,0) ;
}
.introPara {
    font-size: large;
    font-weight: 300;
    letter-spacing: 1px;
}


.btn{
    background:  	rgb(255,215,0);
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.5rem;
    height: 1rem;
}
@media screen and (max-width:840px) {
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}
@media screen and (max-width:440px) {
    .bg{
        right: -20vw;
    }

}