.navbar{
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    max-width: 75rem;
    padding: 1rem 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    background: rgba(10, 10, 10, 0.532);

}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktoMenuListItem {
    margin: 1rem;
    cursor: pointer;

}
.desktoMenuListItem:hover{
    color:	rgb(255,215,0);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid 	rgb(255,215,0);
}
.desktopMenuBtn {
    background: 	rgb(255,215,0);
    color: #000;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius:2rem ;
}
.desktopMenuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}

.active {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 2rem;
}
.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.listItem{
    color:silver;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

@media screen and (max-width:720px) {
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
    
}