

#works{
    margin: 0 auto;
    min-height: calc(100vh -4rem);
    width: 100%;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem ;
}
.WorkBtn:hover {
    color: rgb(255, 215, 0);
    border: 5px solid rgb(255, 215, 0);
}
.WorkBtn {
    margin-top: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    vertical-align: top;
    color: #000;
    border: none;
    padding: 1rem;
    text-align: center;
    color: silver;

}
.worksTitle{
    margin: 1rem 0;
    font-size: 3rem;

}

.workDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}

.workImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    flex-wrap: wrap;
    max-width: 65rem;
} 
.worksImg {
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}

@media screen and (max-width:585px) {
    .workImgs{
        height: 50vw;

    }
}